import { _decorator, Component, Node, Prefab, instantiate, log, Gradient, Graphics } from 'cc';
import { level_progress_bar } from '../../transiton/level_progress_bar';
const { ccclass, property } = _decorator;

@ccclass('level4_science')
export class level4_science extends Component {
    @property(level_progress_bar)
    levelBarUI: level_progress_bar = null;
    @property(Node)
    level: Node = null;
    @property(Prefab)
    gPrefab: Prefab = null;

    private graphics = null;
    private draw = null;
    start() {
        this.initGraphic();
        this.draw 
    }
    initGraphic(){
        let nodeG = instantiate(this.draw);
        log("nodeG", nodeG);
        this.graphics = nodeG.getComponent(Graphics);
        let g = this.graphics;
        g.lineWidth = 10;
        // this.graphics = 
    }

    update(deltaTime: number) {
        
    }
}


